<template>
  <div class="list">
    <el-table :data="tableData" border  style="width: 100%">
        <el-table-column prop="index" label="轮播图片(轮播顺序--从上到下)" width="798px">
            <template slot-scope="scope">
                <img :src="scope.row.img" alt="">
            </template>
        </el-table-column>
        <el-table-column label="操作" width="300px">
            <template slot-scope="scope">
                <el-button
                size="mini"
                @click="edit(scope.row._id)">编辑</el-button>
                <el-button
                size="mini"
                type="danger"
                @click="del(scope.row._id)">删除</el-button>
            </template>
        </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
      return {
          tableData:[]
      }
  },
  methods:{
      getData() {
          this.$axios.get('getSwiper').then(res => {
              if(res.code == 200){
                  this.tableData = res.data
              }
          })
      },
      del(id) {
          this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
          }).then(() => {
                this.$axios.post('delSwiper',{id}).then(res => {
                    if(res.code == 200) {
                        this.$message({
                        type: 'success',
                        message: '删除成功!'
                        });
                        setTimeout(() => {
                            this.$router.go(0)
                        },1000)
                    }
                }) 
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
      },
      edit(id) {
          this.$router.push({path:'/swiper/update',query:{id}})
      }
  },
  created() {
      this.getData()
  }
}
</script>
<style scoped>
.list{
    margin: 20px 10px;
}
img{
    max-height:200px;
}
</style>



